/* playfair-display-regular - latin */
@font-face {
	font-family: "Playfair Display";
	font-style: normal;
	font-weight: 400;
	src: url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.eot"); /* IE9 Compat Modes */
	src: local(""),
		url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.eot?#iefix") format("embedded-opentype"),
		/* IE6-IE8 */ url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.woff2") format("woff2"),
		/* Super Modern Browsers */ url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.woff")
			format("woff"),
		/* Modern Browsers */ url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.ttf") format("truetype"),
		/* Safari, Android, iOS */ url("/dist/assets/fonts/Playfair/playfair-display-v21-latin-regular.svg#PlayfairDisplay")
			format("svg"); /* Legacy iOS */
}

